import { Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { addCount, deleteProduct } from './../store/userSlice.js';
import { memo, useState } from 'react';

// memo로 쓸데없이 재렌더링 막기
let Child = memo(function(){
    return <div>자식임</div>
})

function Cart() {

    let state = useSelector((state) => { return state });
    console.log(state.product);

    let [count, setCount] = useState(0)

    let dispatch = useDispatch()    // store.js로 요청 보냄

    return (
        <div>
            <Child></Child>
            <button onClick={()=>{setCount(count+1)}}>+</button>
            {state.user}의 장바구니
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>상품명</th>
                        <th>수량</th>
                        <th>변경하기</th>
                        <th>수량+1</th>
                        <th>삭제</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        state.product.map((elem, index) => {
                            return (
                                <tr key={elem.id}>
                                    <td>{elem.id}</td>
                                    <td>{elem.name}</td>
                                    <td>{elem.count}</td>
                                    <td>안녕</td>
                                    <td>
                                        <button onClick={()=>{
                                            dispatch(addCount(elem.id))
                                        }}>+</button>
                                    </td>
                                    <td>
                                        <button onClick={()=>{
                                            dispatch(deleteProduct(index))
                                        }}>삭제</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
        </div>
    )
}

export default Cart